import { Component, OnInit } from '@angular/core';
import { Model } from 'src/app/api/models/model';
import { ActivatedRoute, Router } from '@angular/router';
import { RequestService } from 'src/app/api/request.service';
import { DialogService } from 'src/app/api/util/dialog.service';
import { AuthUserService } from 'src/app/services/auth-user.service';
import { ModalController, ActionSheetController } from '@ionic/angular';
import { UtilArrayService } from 'src/app/services/util-array.service';
import { FormExpensePage } from '../form-expense/form-expense.page';
import { UtilService } from 'src/app/services/util.service';

@Component({
  selector: 'app-list',
  templateUrl: './list.page.html',
  styleUrls: ['./list.page.scss'],
})
export class ListPage implements OnInit {

  public team_id ;
  public modelBalanceSheet : Model;
  public modelTeam : Model;
  public modelAmountTeam : Model;

  public team : any;

  constructor(
    public route : ActivatedRoute,
    private router : Router,
    private requestService : RequestService,
    public dialogService : DialogService,
    public authUser : AuthUserService,
    private modalController : ModalController,
    public utilArray : UtilArrayService,
    public actionSheetController: ActionSheetController,
    public util : UtilService
  ) { 
    
    this.modelBalanceSheet =  new Model('BalanceSheet',requestService)
    this.modelTeam = new Model('Team', requestService);
    this.modelAmountTeam = new Model('AmountTeam',requestService);

    this.team_id = this.route.snapshot.paramMap.get('team_id');

  }

  ngOnInit() {
      this.init();
    
  }

  async init(){
    await this.dialogService.loadingProgres();

    this.modelTeam.api_functionModel(this.team_id,'pageExpenses').subscribe(
      response => {
        //console.log('response Team',response);
        if(response['status'] == 'success'){
          this.team = response['data'];
        }
        this.dialogService.dimissLoaging();
      },
      error => {
        this.dialogService.dimissLoaging()
      }
    );
    
  }



  async showOptions(expense){

    let buttons = [];

    buttons.push( {
      text: 'Pagar',
      icon: 'cash',
      handler: () => {
        console.log('Cancel clicked');
        //this.paid(expense);
      }
    });

    buttons.push( {
      text: 'Ver meses',
      icon: 'calendar',
      handler: () => {
        console.log('Ver meses clicked');
        this.router.navigateByUrl('/balance-sheet/months/' + expense.id);
      }
    });

    buttons.push( {
      text: 'Ver Pagos',
      icon: 'wallet',
      handler: () => {
        console.log('Ver meses clicked');
        this.router.navigateByUrl('/balance-sheet/payments/' + expense.id);
      }
    });

    /*
    buttons.push( {
      text: 'Modificar',
      icon: 'create-outline',
      handler: () => {
        console.log('Edit clicked');
      }
    })
*/
    buttons.push({
      text: 'Eliminar',
      role: 'destructive',
      icon: 'trash',
      handler: () => {
        this.deleteBalanceSheet(expense);
        console.log('Delete clicked');
      }
    })
    
    
    buttons.push( {
      text: 'Cancelar',
      icon: 'close',
      role: 'cancel',
      handler: () => {
        console.log('Cancel clicked');
      }
    });

    const actionSheet = await this.actionSheetController.create({
      header: 'Operaciones de : ' + expense.user.completeName,
      cssClass: 'my-custom-class',
      buttons: buttons
    });
    await actionSheet.present();
  }

  /**
   * Elimina la Hoja de balance de un usuario
   */
  async deleteBalanceSheet(expense){
    await this.dialogService.loadingProgres('Eliminando..');

    this.modelBalanceSheet.api_delete(expense.id).subscribe(
      response => {
        this.dialogService.dimissLoaging();
        this.utilArray.listDelete(this.team.expenses,expense.id);
      },
      error => {
        this.dialogService.dimissLoaging();
        //this.dialogService.showToast('Error al eliminar Registro, tiene registos asociados');
      }
    )
  }


  async add(){
    const modal = await this.modalController.create({
      component: FormExpensePage ,
      componentProps : {
        team_id : this.team_id,
        amount_of_the_month : this.team.amount_balance
      }
    });

    modal.onDidDismiss().then(data=>{
      if(data.data && data.data['expense']){
        const expense = data.data['expense'];
        //this.playerService.listAddLast(player);
        this.utilArray.listAddFirst(this.team.expenses , expense);
      }
    })

    return await modal.present();
  }



}
